import {Container} from "../common/Container/Container";
import React, {useState} from "react";
import style from "./howMuchIs.module.scss"
import {H2} from "../common/H2/H2";
import clsx from "clsx";
import {useDispatch, useSelector} from "react-redux";
import {appAC, selectLang, selectThemeType} from "../../store/reducers/app.reducer";
import {svgIcons} from "../../assets/svg/svgIcons";
import {translate} from "../../utils/lang";
import {HowMuchIsModal} from "../A3_Modals/HowMuchIsModal/HowMuchIsModal";

export interface ICard {
    title: string
    text: string
    price: number
    background: string
    icon: any
}

const cards: ICard[]  = [
    {
        title: "Landing Page",
        text: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor.",
        price: 200,
        background: "#0085FF",
        icon: svgIcons.howMuchIs0,
    },
    {
        title: "Corporative",
        text: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor.",
        price: 270,
        background: "#00FFAF",
        icon: svgIcons.howMuchIs1,
    },
    {
        title: "E-commerce",
        text: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor.",
        price: 380,
        background: "#F9C835",
        icon: svgIcons.howMuchIs2,
    },
];


export const HowMuchIs = () => {
    const themeType = useSelector(selectThemeType);
    const lang = useSelector(selectLang);
    const [card, setCard] = useState<null | ICard>(null);

    const dispatch = useDispatch();

    return (
        <Container classNameInner={style.howMuchIs}>

            <HowMuchIsModal card={card}/>

            <H2 text="How much is"/>

            <div className={style.cards}>
                {
                    cards.map((card, index) => (
                        <div key={index}
                             className={clsx({
                                 [style.card]: true,
                                 [style.card_light]: themeType === "light",
                                 [style.card_dark]: themeType === "dark",
                             })}
                        >
                            <div className={style.iconWrapper}>
                                <div className={style.back}
                                     style={{background: card.background}}
                                />
                                {card.icon}
                            </div>

                            <p className={style.title}>
                                {translate(card.title, lang)}
                            </p>

                            <p className={style.text}>
                                {translate(card.text, lang)}
                            </p>

                            <p className={style.price}
                               style={{color: card.background}}
                            >
                                {`${card.price}$`}
                            </p>

                            <button className={style.btn}
                                    onClick={() => {
                                        setCard(card);
                                        dispatch(appAC.setShowModal({show: true, type: 'how much is'}));
                                    }}
                            >
                                {translate("Make an order", lang)}
                            </button>

                        </div>
                    ))
                }
            </div>

        </Container>
    )
}